<template>
  <div class="setAppid">
    <h2 class="title">设置appid</h2>

    <!-- ele UI -->
    <div class="setAra">
      <el-form :label-position="labelPosition" label-width="80px" :model="formLabelAlign">
        <el-form-item label-width='120px' v-for='(value,key,index) in  formLabelAlign' :key='index' :label="key">
          <el-input v-model="formLabelAlign[key]"></el-input>
        </el-form-item>


        <el-form-item label-width='120px' label="设置读者类型">
          <el-select v-model="formLabelAlign.rdrtype" placeholder="请选择">
            <el-option v-for="item in columns" :key="item.code" :label="item.name" :value="item.code">
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
    </div>
    <div class="btn">
      <el-button type="info" plain @click='submitFn'>{{btn_txt}}</el-button>
    </div>

  </div>

</template>
<script>
  export default {
    name: 'SetAppid',
    data() {
      return {
        btn_txt: '提交',
        labelPosition: 'left',
        formLabelAlign: {

        },

        columns: null
      }

    },
    methods: {
      async fetchWechat() {
        let res = await this.$store.dispatch('fetchConfig')
        console.log(res)
        let resstr = JSON.stringify(res.data)
        if (resstr !== '{}') {
        
          let wechat = res.data.wechat
          let elib = res.data.elib
          let other = {
            title: res.data.title,
            redirect_uri: res.data.redirect_uri,
            dbhost: res.data.dbhost
          }

          this.formLabelAlign = Object.assign({}, wechat, elib, other)

          if (this.formLabelAlign && this.formLabelAlign.AppID) {
            this.btn_txt = '修改'
          }

        } else {
          console.log('空数据')

          this.formLabelAlign = {
            title: '站点标题',
            redirect_uri: '填写公众号网页URL',
          
            
            appID: '公众号的appid',
            appSecret: '公众号的app secret',
            token: '公众号开发设置的 token',
            mch_id:'微信支付的商户号',
            key:'微信商户号支付密钥key',

            account: 'elib后台帐户',
            password: 'elib后台帐户密码',
            client_id: 'elib后台连接 id',
            baseurl: 'elib部署的url',
            other_url: '其他 配合eilb的url',
            autordrno: '读者条码区间',
            rdrtype: '读者类型'



          }
        }



        // this.getSystemInfo()


      },
      async submitFn() {
        console.log('存储/修改 配置信息')

        let data = this.formLabelAlign
        let res = await this.$store.dispatch('setConfig', data)
        this.getSystemInfo()
      },
      async getSystemInfo() {

        let param = {
          key: 'rdrtype'
        }
        let res = await this.$store.dispatch('systemInfo', param)
        let rdr_type = res.data.rows
        this.rdr_type = rdr_type
        let columns = []
        for (let i = 0; i < rdr_type.length; i++) {
          let {
            code,
            group,
            name
          } = rdr_type[i]

          columns[columns.length] = {
            code,
            group,
            name
          }
        }
        this.columns = columns

      }
    },
    mounted() {
      console.log('设置appid mounted（）')
      this.fetchWechat()

    },
  }

</script>

<style scoped lang="scss">
  .setAppid {
    background: rgba(255, 255, 255, 1);
    border-radius: 15px;
    box-shadow: 0px 2px 22px 0px rgba(0, 0, 0, 0.08);
    padding: 10px;

    .btn {
      text-align: center;
      margin: 20px auto;
    }
  }

</style>
