<template>
    <div class="comment">
            <div class="title">评论留言</div>

            <div class="content">
                <div class="item" v-for='(item,index) in comments'>
                    <div class="left">
                        <img :src="item.up_author.avatar" alt="头像">
                        <div class="nickname">{{item.up_author.nickname}}</div>
                    </div>
                    <div class="right">
                        <div class="text">{{item.content}}</div>
                        <div class="time">{{item.format_time}}</div>
                    </div>
                </div>
            </div>
    </div>
</template>


<script>
       function dateFormat(fmt, date) {
        let ret;
        let opt = {
            "Y+": date.getFullYear().toString(), // 年
            "m+": (date.getMonth() + 1).toString(), // 月
            "d+": date.getDate().toString(), // 日
            "H+": date.getHours().toString(), // 时
            "M+": date.getMinutes().toString(), // 分
            "S+": date.getSeconds().toString() // 秒
            // 有其他格式化字符需求可以继续添加，必须转化成字符串
        }
        for (let k in opt) {
            ret = new RegExp("(" + k + ")").exec(fmt);
            if (ret) {
                fmt = fmt.replace(ret[1], (ret[1].length == 1) ? (opt[k]) : (opt[k].padStart(ret[1].length, "0")))
            }
        }
        return fmt
    }

    export default{


        data(){
            return{
                comments:null
            }
        },
        methods: {
            async getAllComment(){

                // 获取 所有用户的留言/建议/投诉
                let res = await this.$store.dispatch('allComment')
                this.comments = res.data
                console.log(res)

                this.comments.forEach(item => {
                    item.meta.createdAt = new Date(item.meta.createdAt)

                    item['format_time'] = dateFormat("YYYY-mm-dd HH:MM", item.meta.createdAt)
                });


            }
        },
        mounted() {
            this.getAllComment()
        },
    }

</script>

