<template>
  <div class="setServer">
    <div class="head">
      <h2>设置微信服务大厅</h2>
    </div>
    <div class="topCarousel">

      <div class="contentTitle">
        <h3>设置轮播图</h3>
      </div>

      <div class="currentset" >
        <img width="100%" v-for='item in imgList' :src="item.imgurl" alt="" :key='item._id'>
      </div>
      <div class="set" >
        <el-upload :action="domain" list-type="picture-card" :limit='5' :auto-upload='true' :file-list="fileList"
          ref="upload" :http-request=carousel :on-preview="handlePictureCardPreview" :on-remove="handleRemove">
          <i class="el-icon-plus"></i>
        </el-upload>
        <el-dialog :visible.sync="dialogVisible">
          <img width="100%" :src="dialogImageUrl" alt="">
        </el-dialog>
      </div>
      <button @click='updateCar'>更新轮播图</button>
    </div>
    <!-- 上传轮播图 -->
    <div class="content" v-if='apps'>
      <h3>设置服务大厅应用</h3>
      <div style="margin: 20px;"></div>
      <div class="wrap">
        <div class="app" v-for='(app,index) in apps ' :key="index">
          <el-form label-position="left" label-width="40px" :model="app" class="from">
            <el-form-item :label="key=='name'?'名称':key" class="form_item" v-for='(value,key,index) in  app'
              :key='index'>
              <el-input v-if="key !== 'icon'" v-model="app[key]"></el-input>
              <el-upload v-else class="avatar-uploader" :limit='1' :action=domain :http-request=upqiniu
                :show-file-list="false" :before-upload="beforeUpload">
                <img v-if="app.icon" :src="app.icon" class="avatar">
                <i v-else class="el-icon-plus avatar-uploader-icon" @click='getIndex(app.name)'></i>
              </el-upload>
            </el-form-item>
            <el-form-item>
              <el-button v-if="app._id" class="app_btn" size="small" type="primary" @click="delApp(app._id)">
                删除此应用</el-button>
              <el-button v-else class="app_btn" size="small" type="primary" @click="submitForm(app)">保存应用
              </el-button>
            </el-form-item>
          </el-form>
        </div>
      </div>
    </div>

  </div>
</template>
<script>
  export default {
    name: 'server',
    data() {
      return {
        apps: null,
        curname: '',
        imageUrl: '',
        token: {},
        domain: 'https://upload-z2.qiniup.com',
        qiniuaddr: 'lp-cdn.js-shrfid.com',


        dialogImageUrl: '',
        dialogVisible: false,
        carouselimg: [],
        fileList: [],
        limit: 0,
        imgList: null,
        setshow: false
      }
    },


    methods: {
      handleRemove(file, fileList) {

        this.fileList = fileList
      },
      handlePictureCardPreview(file) {
        this.dialogImageUrl = file.url;
        this.dialogVisible = true;
        this.fileList.push(file)
      },
      async carousel(req) {
        const config = {
          headers: {
            'Content-Type': 'multipart/form-data'
          }
        }
        let filetype = ''
        if (req.file.type === 'image/png') {
          filetype = 'png'
        } else {
          filetype = 'jpg'
        }
        const keyname = 'rfid-lp' + '-' + req.file.name
        let res = await this.$store.dispatch('uploadToken')
        const formdata = new FormData()
        formdata.append('file', req.file)
        formdata.append('token', res.data)
        formdata.append('key', keyname)

        this.$http.post(this.domain, formdata, config).then(async ress => {
          console.log('上传成功')
          this.limit++
          let imageUrl = 'http://' + this.qiniuaddr + '/' + ress.data.key
          this.dialogImageUrl = imageUrl
          let item = {
            imgurl: imageUrl
          }
          let resss = await this.$store.dispatch('setCarousel', item)
        })
      },


      beforeUpload() {
        console.log('beforeUpload')
      },
      getIndex(name) {
        this.curname = name
      },

      async delApp(id) {
        let res = await this.$store.dispatch('delApp', {
          id
        })
        console.log(res)
        if (res.statusText === 'OK') {
          this.getApps()
        }
      },
      async submitForm(app) {
        let urlTo = app.url || app.to
        if (!app.name || !app.icon || !urlTo) {
          return this.$message('信息不完整')
        }
        let res = await this.$store.dispatch('setServer', app)
        let tem = {
          name: '',
          icon: '',
          url: '',
          _id: '',
          to: '',
        }
        if (res) {
          this.getApps()
        }
      },

      async getApps() {
        let res = await this.$store.dispatch('getApps')
        let apps = res.data
        let arr = []
        let tem = {
          name: '',
          icon: '',
          url: '',
          _id: '',
          to: '',
        }
        if (apps.length != 0) {
          for (let i = 0; i < apps.length; i++) {
            let {
              name,
              icon,
              url,
              _id,
              to
            } = apps[i]
            arr[arr.length] = {
              name,
              icon,
              url,
              _id,
              to
            }

          }
          arr.push(tem)

          this.apps = arr

        } else {
          arr.push(tem)

          this.apps = arr

        }
        return arr
      },
      async upqiniu(req) {
        const config = {
          headers: {
            'Content-Type': 'multipart/form-data'
          }
        }
        let filetype = ''
        if (req.file.type === 'image/png') {
          filetype = 'png'
        } else {
          filetype = 'jpg'
        }
        const keyname = 'rfid-lp' + '-' + req.file.name
        let res = await this.$store.dispatch('uploadToken')
        const formdata = new FormData()
        formdata.append('file', req.file)
        formdata.append('token', res.data)
        formdata.append('key', keyname)

        this.$http.post(this.domain, formdata, config).then(ress => {
          let imageUrl = 'http://' + this.qiniuaddr + '/' + ress.data.key

          this.apps.forEach(element => {

            if (element.name == this.curname) {
              element.icon = imageUrl
            }
          });
        })

      },
      async getCarousel() {
        console.log('获取轮播图')
        let res = await this.$store.dispatch('getCarousel')
        let imgList = []
        for (let i = 0; i < res.data.length; i++) {
          let {
            imgurl
          } = res.data[i]
          imgList.push({
            imgurl
          })
        }
        this.imgList = imgList
        console.log(imgList)
      },
      async updateCar(){
            this.setshow = true
            let res = await this.$store.dispatch('delCarousel',{})

      }
    },
    mounted() {

    },

    created() {
      this.getApps()
      this.getCarousel()
    }
  }

</script>

