import fly from './ajax'

async function test(param){
    let res = await fly.get('/test',{param})
    return res
}
async function setConfig(params){
    let res = await fly.post('/setConfig',params)
    return res
}

async function fetchConfig(){
    let res = await fly.get('/fetchConfig')
    return res
}

async function getMenu(){
    let res = await fly.get('/menulist')
    return res
}
async function getMedia(){
    let res = await fly.get('/batchMaterial')
    return res
}
async function updateMenu(menu){
    let res = await fly.post('/updateMenu',menu)
    return res
}

async function setServer(params){
    let res  = await fly.post('/setServer',params)
    return res
}

async function getApps( ) { 

    let res = await fly.get('/getApps')
    return res
 }
 async function delApp(params) {
     let res = await fly.get('/delApp',params)
     return res
 }
 async  function uploadToken(params) {
     let res = await fly.get('/uploadToken')
     return res
 }

 async function setCarousel(params) {
     let res = await fly.post('/setCarousel',params)
     return res
 }
 async function delCarousel(params) {
    let res = await fly.get('/delCarousel',params)
    return res
}
async function getCarousel () {
    let res = await fly.get('/getCarousel')
    return res
    
}
// 获取后台系统信息
async function systemInfo(params) {
    let res = await fly.get('/systemInfo', params)
    return res
}

//
async function allComment(){
    let res = await fly.get('/allComment')
    return res
}
export default{
    test,
    setConfig,
    fetchConfig,
    getMenu,
    getMedia,
    updateMenu,
    setServer,
    getApps,
    delApp,
    uploadToken,
    setCarousel,
    delCarousel,
    getCarousel,
    systemInfo,
    allComment 
}